.navbar{
    height: 100px;
    
    .wrapper{
        max-width: 1300px;
        margin:auto;
        justify-content: space-between;
        display: flex;
        align-items: center;
        height: 100%;
    }
    span{
        font-weight: bold;
        font-size: 30px;
        align-items: center;
        color: grey;
    }
    .social{
        display: flex;
        gap:20px;

        SocialIcon{
            width:10;
            height:10;
        }

        .tooltip-text {
            visibility: hidden;
            position: absolute;
            z-index: 1;
            width: 100px;
            color: white;
            background-color: #192733;
            font-size: 15px;
            padding: 5px;
          }
          
          .hover-text:hover .tooltip-text {
            visibility: visible;
          }
          
          .bottom {
            top: 50px;
            left: -50%;
          }

          .hover-text {
            position: relative;
            // display: inline-block;
            size:1px;
            text-align: center;
          }
    }
    
    
}
